import { Routes } from '@angular/router';

export const routes: Routes = [
{path: "initialGrid", loadComponent: () => import("./features/initial_grid.component").then((c) => c.InitialGridComponent)},
{path: "battleGrid", loadComponent: () => import("./features/battle_grid.component").then((c) => c.BattleGridComponent)},
  {path: "connectPlayer", loadComponent: () => import("./features/connect_player.component").then((c) => c.ConnectPlayerComponent)},
{path: "", redirectTo: "account", pathMatch: "full"}
];
